<template>
  <v-container grid-list-xl class="customize-product-component">
    <preloader v-if="$store.state.products.isLoading"></preloader>
    <v-layout v-else row justify-space-around wrap>

      <v-flex xs1>
        <v-layout column>
          <v-card
            class="color-item"
            v-for="item in product.colors"
            :key="item.slug"
            @click="selectCurrentColor(item)"
          >
            <v-img :src="item.images[0].url"></v-img>
          </v-card>
        </v-layout>
      </v-flex>

      <v-flex xs9 sm5>
        <v-card v-if="currentColor.images">
          <div class="image-wrapper">
            <img :src="currentColorUrl" class="image-product" />
            <v-img
              :src="currentDesign.activeImage"
              class="image-logo"
              :contain="true"
              :style="{ left: currentColor.images[0].margins.left,
                      right: currentColor.images[0].margins.right,
                      top: currentColor.images[0].margins.top,
                      bottom: currentColor.images[0].margins.bottom }"
              :position="currentColor.images[0].align.horizontal + ' ' + currentColor.images[0].align.vertical"
            ></v-img>
          </div>
        </v-card>
      </v-flex>

      <v-flex xs10 sm6>
        <h3 class="product-name headline mb-3">{{ product.name }}</h3>
        <p class="description subheading">
          {{ product.description }}
        </p>
        <p>
          <v-select
            attach
            :items="product.sizes"
            label="Pick a size"
            item-text="name"
            item-value="name"
            solo
          ></v-select>
        </p>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  import Preloader from "../components/Preloader";

  export default {
    name: 'CustomizeProductPage',
    components: {Preloader},
    data() {
      return {
        currentColor: {}
      }
    },
    created() {
      this.loadData();
    },
    watch: {
      '$route.params': 'loadData'
    },
    computed: {
      product() {
        return this.$store.state.products.current || {};
      },
      currentColorUrl() {
        return this.currentColor && this.currentColor.images && this.currentColor.images[0].url;
      },
      currentDesign() {
        if (this.$store.state.namesList.isLoading) return {};
        let currentDesign = this.$store.state.designs.current;
        if (!currentDesign) return {};
        let name = this.$route.params.name;
        let noImages = this.$store.state.designs.noImages[name] || [];
        let namesList = this.$store.state.namesList.list;
        if (!namesList.find(item => item.slug === name)) {
          return Object.assign(currentDesign, { activeImage: currentDesign.image });
        }
        if (noImages.indexOf(currentDesign.slug) !== -1) {
          return Object.assign(currentDesign, { activeImage: currentDesign.image });
        }
        // replace logo default variant with demo, if it wasn't loaded
        return Object.assign(currentDesign, { activeImage: currentDesign.image.replace(/name(.[\w]+$)/, name + '$1') });
      }
    },
    methods: {
      loadData() {
        this.$store.dispatch('fetchIfEmpty', { key: 'namesList' });
        let productCategory = this.$route.params.productCategory || 'adult';
        this.$store.dispatch('fetchIfEmpty', { key: 'products', slug: productCategory }).then(() => {
          this.$store.commit('setCurrentProduct', this.$route.params.product);
          this.currentColor = this.product.colors[0];
        });
        this.$store.dispatch('fetchIfEmpty', { key: 'designs', slug: this.$route.params.subcategory }).then(() => {
          this.$store.commit('setCurrentDesign', this.$route.params.design);
        });
      },
      selectCurrentColor(color) {
        this.currentColor = color;
      }
    }

  }
</script>

<style lang="scss">
.customize-product-component {
  .product-name {
    text-align: left;
  }
  .description {
    line-height: 1.4;
    text-align: left;
  }
  .color-item {
    cursor: pointer;
  }
  .image-wrapper {
    position: relative;
    display: inline-block;
  }
  .image-logo {
    position: absolute;
  }
  .image-product {
    width: 100%;
  }
}
</style>
