import Vue from 'vue';
import Vuex from 'vuex';
import Backend from './services/backend';
import createLogger from 'vuex/dist/logger';

const backend = new Backend();

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    home: {
      dialogIsOpen: false
    },
    namesList: {
      list: [],
      isLoading: false,
      error: null,
      current: null
    },
    designCategories: {
      list: [],
      isLoading: false,
      error: null,
      current: null,
      currentSubcategory: null
    },
    designs: {
      list: [],
      isLoading: false,
      error: null,
      current: null,
      designSubcategorySlug: null,
      noImages: {}
    },
    productCategories: {
      list: [],
      isLoading: false,
      error: null,
      current: null,
      dialogIsOpen: false,
      currentDialogCategory: null
    },
    products: {
      list: [],
      isLoading: false,
      error: null,
      current: null,
      productSubcategorySlug: null
    },
  },
  mutations: {
    openHomeDialog(state) {
      state.home.dialogIsOpen = true;
    },
    closeHomeDialog(state) {
      state.home.dialogIsOpen = false;
    },
    openProductsDialog(state, currentDialogCategory) {
      state.productCategories.dialogIsOpen = true;
      state.productCategories.currentDialogCategory = currentDialogCategory;
    },
    closeProductsDialog(state) {
      state.productCategories.dialogIsOpen = false;
      state.productCategories.currentDialogCategory = null;
    },
    setName(state, value) {
      // if name selected from list
      if (value && typeof value === 'object') {
        state.namesList.current = value;
      } // if name is custom
      else {
        state.namesList.current = {
          name: value,
          slug: null
        }
      }
    },

    // DESIGN CATEGORIES
    setDesignCategory(state, value) {
      state.designCategories.current = value;
    },
    setDesignSubcategory(state, value) {
      state.designCategories.currentSubcategory = value;
    },

    // DESIGNS
    setCurrentDesign(state, slug) {
      state.designs.current = state.designs.list.find((item) => item.slug === slug);
    },
    setNoDesignImage(state, { nameSlug, designSlug } ) {
      if (state.designs.noImages[nameSlug]) {
        state.designs.noImages[nameSlug].push(designSlug);
      } else {
        state.designs.noImages[nameSlug] = [designSlug];
      }
    },

    //PRODUCTS
    setCurrentProduct(state, slug) {
      state.products.current = state.products.list.find((item) => item.slug === slug);
    },

    // FETCH
    fetchStart(state, { key, slug}) {
      state[key].isLoading = true;
      if (key === 'designs') {
        state.designs.designSubcategorySlug = slug;
      }
      if (key === 'products') {
        state.products.productSubcategorySlug = slug;
      }
    },
    fetchSuccess(state, { key, data, slug }) {
      state[key].isLoading = false;
      state[key].error = null;
      state[key].list = data;
    },
    fetchError(state, { key, error }) {
      state[key].isLoading = false;
      state[key].error = error;
      if (key === 'designs') {
        state.designs.designSubcategorySlug = null;
      }
      if (key === 'products') {
        state.products.productSubcategorySlug = null;
      }
    },


  },
  actions: {
    fetch({ commit }, { key, slug }) {
      return new Promise((resolve, reject) => {
        let keyCapitalized = key.replace(/^\w/g, l => l.toUpperCase());
        commit(`fetchStart`, { key, slug });
        backend[`get${keyCapitalized}`](slug)
          .then(res => {
            commit(`fetchSuccess`, { key, slug, data: res.data });
            resolve({ data: res.data });
          })
          .catch(error => {
            commit(`fetchError`, { key, error: handleErrorMessage(error) });
            reject({ error: handleErrorMessage(error) });
          })
      })
    },
    fetchIfEmpty({ commit, state, dispatch }, { key, slug }) {
      if (key === 'designs' && state.designs.designSubcategorySlug === slug) return;
      if (key === 'products' && state.products.productSubcategorySlug === slug) return;
      if (!state[key].list || !state[key].list.length || key === 'designs' || key === 'products') {
        return dispatch('fetch', { key, slug })
      }
    }
  },
  getters: {
    designSubcategories: state => {
      let currentCategory = state.designCategories.current && state.designCategories.current.slug;
      if (!currentCategory) return [];
      return state.designCategories.list.find((item) => item.slug === currentCategory).subcategories;
    }
  },
  //plugins: [createLogger()]
});

function handleErrorMessage(error) {
  return error.response && error.response.data &&
    JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
    'Something went wrong. Please try again later';
}
