<template>
<v-container grid-list-xl class="products-component">
  <v-layout column>
    <ProductCategories class="mb-5"/>
    <h3 class="text-uppercase headline">Products</h3>
    <v-layout mt-4 row fill-height wrap >
      <v-layout v-if="isLoading" justify-center row>
        <Preloader />
      </v-layout>
      <v-flex xs12 sm6 md4 lg3 v-for="item in products" class="card">
        <v-card
          class="design-card"
          tile
          flat
          :to="{
            name: 'product',
            params: Object.assign($route.params, {
              productCategory: $route.productCategory || 'adult',
              product: item.slug
              })
          }"
        >
          <v-layout justify-center>
            <v-flex xs10>
              <div class="image-wrapper">
                <img :src="item.colors[0].images[0].url" class="image-product" />
                <v-img
                  :src="currentDesign.activeImage"
                  class="image-logo"
                  :contain="true"
                  :style="{ left: item.colors[0].images[0].margins.left,
                      right: item.colors[0].images[0].margins.right,
                      top: item.colors[0].images[0].margins.top,
                      bottom: item.colors[0].images[0].margins.bottom }"
                  :position="item.colors[0].images[0].align.horizontal + ' ' + item.colors[0].images[0].align.vertical"
                ></v-img>
              </div>
            </v-flex>
          </v-layout>
          <span class="headline">{{ item.name }}</span>
        </v-card>
      </v-flex>
    </v-layout>
  </v-layout>

</v-container>
</template>

<script>
import ProductCategories from '../components/ProductCategories';
import Preloader from '../components/Preloader';

export default {
  name: 'ProductsPage',
  components: {
    ProductCategories,
    Preloader
  },
  watch: {
    '$route.params': 'loadData'
  },
  created() {
    this.loadData();
  },
  computed: {
    products() {
      return this.$store.state.products.list;
    },
    isLoading() {
      return this.$store.state.productCategories.isLoading;
    },
    currentDesign() {
      if (this.$store.state.namesList.isLoading) return {};
      let currentDesign = this.$store.state.designs.current;
      if (!currentDesign) return {};
      let name = this.$route.params.name;
      let noImages = this.$store.state.designs.noImages[name] || [];
      let namesList = this.$store.state.namesList.list;
      if (!namesList.find(item => item.slug === name)) {
        return Object.assign(currentDesign, { activeImage: currentDesign.image });
      }
      if (noImages.indexOf(currentDesign.slug) !== -1) {
        return Object.assign(currentDesign, { activeImage: currentDesign.image });
      }
      // replace logo default variant with demo, if it wasn't loaded
      return Object.assign(currentDesign, { activeImage: currentDesign.image.replace(/name(.[\w]+$)/, name + '$1') });
    }
  },
  methods: {
    loadData() {
      this.$store.dispatch('fetchIfEmpty', { key: 'namesList' });
      let productCategory = this.$route.params.productCategory || 'adult';
      this.$store.dispatch('fetchIfEmpty', { key: 'products', slug: productCategory });
      this.$store.dispatch('fetchIfEmpty', { key: 'designs', slug: this.$route.params.subcategory }).then(() => {
        this.$store.commit('setCurrentDesign', this.$route.params.design);
      });
    }
  }
};
</script>
<style lang="scss">
  .products-component {
    .image-wrapper {
      position: relative;
      display: inline-block;
    }
    .image-logo {
      position: absolute;
    }
    .image-product {
      width: 100%;
    }
  }
</style>
