<template>
  <v-dialog
    class="product-categories-dialog-component"
    v-model="dialogIsOpen"
    persistent
  >
    <v-card class="dialog-card">
      <v-layout :style="{ height: 0 }" justify-end>
        <v-icon
          x-large
          @click="closeDialog"
        >
          close
        </v-icon>
      </v-layout>
      <v-layout justify-center>
        <h2 class="headline">Choose a Surface</h2>
      </v-layout>
      <v-layout row wrap>
        <v-flex xs6 md4 v-for="item in productSubcategories" :key="item.slug">
          <v-card flat class="category-card ma-3"  @click="selectProductCategory(item.slug)">
            <v-img :src="item.image"></v-img>
            <div class="mt-3">
              <span>{{ item.name }}</span>
            </div>
          </v-card>
        </v-flex>
      </v-layout>

    </v-card>
  </v-dialog>
</template>

<script>
import Backend from '../services/backend';

const backend = new Backend();

export default {
  name: 'ProductCategoriesDialog',
  props: ['model'],
  components: {
  },
  data() {
    return {
    };
  },
  computed: {
    dialogIsOpen() {
      return this.$store.state.productCategories.dialogIsOpen;
    },
    productSubcategories() {
      return this.$store.state.productCategories.currentDialogCategory &&
        this.$store.state.productCategories.currentDialogCategory.subcategories;
    }
  },
  created() {
    this.$store.dispatch('fetchIfEmpty', { key: 'designCategories' });
  },
  methods: {
    selectProductCategory(productCategory) {
      this.$router.push({
        name: 'productCategory',
        params: {
          ...this.$router.params,
          productCategory: productCategory,
        }
      });
      this.closeDialog();
    },
    closeDialog() {
      this.$store.commit('closeProductsDialog');
    }
  }
};
</script>

<style lang="scss">
  .product-categories-dialog-component {
    .dialog-card {
      min-height: 90vh;
      padding: 30px 20px;
    }
    .category-card {
      cursor: pointer;
    }
    @media (max-width: 576px) {
      width: 100vw;
    }
  }
</style>
