<template>
  <div class="product-categories-component">
    <Preloader v-if="isLoading" />
    <v-layout row wrap>
      <v-flex
        v-for="item in productCategories"
        :key="productCategories.slug"
      >
        <v-card height="90" @click="openDialog(item)" class="product-category-card">
          <v-img :src="item.image" contain height="60px"></v-img>
          {{ item.name }}
        </v-card>
      </v-flex>
    </v-layout>
    <products-categories-dialog />
  </div>
</template>

<script>
import ProductsCategoriesDialog from './ProductsCategoriesDialog';
import Preloader from "./Preloader";

export default {
  name: 'ProductCategories',
  components: {
    Preloader,
    ProductsCategoriesDialog
  },
  created() {
    this.$store.dispatch('fetchIfEmpty', { key: 'productCategories' });
  },
  data() {
    return {
      model: ''
    };
  },
  computed: {
    productCategories() {
      return this.$store.state.productCategories.list;
    },
    isLoading() {
      return this.$store.state.productCategories.isLoading;
    }
  },
  methods: {
    openDialog(currentCategory) {
      this.$store.commit('openProductsDialog', currentCategory);
    },

    closeDialog() {
      this.$store.commit('closeProductsDialog');
    }
  }
};
</script>

<style lang="scss">
  .product-categories-component {
    .product-category-card {
      cursor: pointer;
    }
  }
</style>
