<template>
  <v-dialog
    class="dialog-big-component"
    v-model="dialogIsOpen"
    persistent
  >
    <v-card class="card">
      <v-layout :style="{ height: 0 }" justify-end>
        <v-icon
          x-large
          @click="closeDialog"
        >
          close
        </v-icon>
      </v-layout>
      <for-who
        v-if="currentStep === 'forWho'"
        @select="onChangeStep"
      />
      <choose-design-category
        v-if="currentStep === 'chooseDesignCategory'"
        @select="onChangeStep"
      />
      <choose-design-subcategory
        v-if="currentStep === 'chooseDesignSubcategory'"
        @select="onChangeStep"
      />


    </v-card>
  </v-dialog>
</template>

<script>
import ForWho from './ForWho';
import ChooseDesignCategory from './ChooseDesignCategory';
import ChooseDesignSubcategory from './ChooseDesignSubcategory';
import Backend from '../../services/backend';

const backend = new Backend();

export default {
  name: 'DialogBig',
  props: ['model'],
  components: {
    ForWho,
    ChooseDesignCategory,
    ChooseDesignSubcategory
  },
  data() {
    return {
      currentStep: 'forWho'
    };
  },
  computed: {
    dialogIsOpen() {
      return this.$store.state.home.dialogIsOpen;
    },
  },
  created() {
    this.$store.dispatch('fetchIfEmpty', { key: 'designCategories' });
  },
  methods: {
    closeDialog() {
      this.$store.commit('closeHomeDialog');
      setTimeout(() => this.currentStep = 'forWho', 1000);
    },
    onChangeStep(step) {
      this.currentStep = step;
    },
  }
};
</script>

<style lang="scss">
  .dialog-big-component {
    .card {
      min-height: 90vh;
      padding: 30px 20px;
    }
    @media (max-width: 576px) {
      width: 100vw;
    }
  }
</style>
